html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Raleway", sans-serif;
}

img {
  display: block;
  max-width: 100%;
  width: auto;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

[class$=__container] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1 {
  margin: 0;
  line-height: 1.2;
  font-family: "Staatliches", cursive;
}

h2 {
  margin: 0;
  line-height: 1.2;
  font-family: "Staatliches", cursive;
}

h3 {
  margin: 0;
  line-height: 1.2;
  font-family: "Staatliches", cursive;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=__heading] {
  margin: 2rem 0;
}

p {
  font-size: 2rem;
  color: #111;
  line-height: 1.8;
}

a {
  color: #111;
  text-decoration: none;
}

.header {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header {
    padding-bottom: 0;
    flex-direction: row;
    justify-content: space-between;
  }
}

.hero {
  min-height: 60rem;
  background-image: url(../assets/img/hero.jpg);
  background-size: cover;
  background-position: center;
  display: grid;
  align-items: center;
}
.hero__text {
  display: grid;
  justify-items: center;
}
.hero__title {
  max-width: 50rem;
  color: #fff;
  text-align: center;
}
.hero__btn {
  margin-top: 2rem;
  display: inline-block;
  padding: 1.5rem 4rem;
  width: 100%;
  background-color: #ff8015;
  font-size: 2rem;
  font-weight: 700;
  color: #fff;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  border: none;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .hero__btn {
    width: auto;
  }
}
@media (hover: hover) {
  .hero__btn:hover {
    background-color: #e16700;
    cursor: pointer;
  }
}

.search {
  background-color: #f2f2f2;
  padding: 5rem 0;
}
.search__heading {
  text-align: center;
  margin: 0;
  color: #484349;
}

.events {
  padding: 5rem 0;
}
.events__heading {
  text-align: center;
  margin: 0;
  color: #484349;
}
.events__grid {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .events__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.categories {
  padding: 5rem 0;
  background-color: #f2f2f2;
}
.categories__heading {
  text-align: center;
  margin: 0;
  color: #484349;
}
.categories__grid {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 4rem;
}
@media (min-width: 768px) {
  .categories__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .categories__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.groups {
  padding: 5rem 0;
  background-image: linear-gradient(to bottom, #b8336a 0%, #b8336a 50%, #068d9d 50%, #068d9d 100%);
  color: #fff;
}
@media (min-width: 768px) {
  .groups {
    background-image: linear-gradient(to right, #b8336a 0%, #b8336a 50%, #068d9d 50%, #068d9d 100%);
  }
}
.groups__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .groups__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.groups__heading {
  font-size: 3rem;
}
.groups__text {
  margin: 0;
  color: #fff;
}

.footer {
  padding: 5rem 0 0;
}
.footer__copyright {
  padding: 1rem;
  text-align: center;
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
  }
}
.nav__link {
  padding: 0.5rem 0;
  display: flex;
  align-items: center;
}
.nav__link--create {
  font-weight: 700;
  color: #b8336a;
}
@media (min-width: 768px) {
  .nav__link--create::after {
    margin-left: 2rem;
    content: "";
    display: inline-block;
    height: 4rem;
    border-right: 0.2rem solid #f2f2f2;
  }
}
@media (hover: hover) {
  .nav__link {
    transition: color 0.2s ease-in-out;
  }
  .nav__link:hover {
    color: #b8336a;
  }
}

.search-form {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .search-form {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.search-form__camp {
  padding: 1rem;
  border: 0.1rem solid #c7c3c8;
  border-radius: 1rem;
  outline: none;
  background-color: #fff;
}
.search-form__btn {
  margin-top: 2rem;
  display: inline-block;
  padding: 1.5rem 4rem;
  width: 100%;
  background-color: #b8336a;
  font-size: 2rem;
  font-weight: 700;
  color: #fff;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  border: none;
  border-radius: 1rem;
  margin-top: 0;
  padding: 1rem;
}
@media (min-width: 768px) {
  .search-form__btn {
    width: auto;
  }
}
@media (hover: hover) {
  .search-form__btn:hover {
    background-color: #902853;
    cursor: pointer;
  }
}

.card-event {
  border: 0.1rem solid #f2f2f2;
}
.card-event__content {
  padding: 2rem;
}
.card-event__date {
  font-size: 1.5rem;
  margin: 0;
}
.card-event__title {
  margin: 1rem 0;
  font-size: 2rem;
  color: #484349;
}
.card-event__author {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.card-event__author-img {
  width: 7rem;
  border-radius: 50%;
  object-fit: cover;
  overflow: hidden;
}
.card-event__organized-text, .card-event__organized-name {
  margin: 0;
}
.card-event__organized-text {
  font-weight: 700;
}

.card-category__name {
  text-align: center;
  font-size: 2.4rem;
  font-weight: 700;
  margin: 1rem;
}

.card-sidebar__content {
  background-color: #068d9d;
  padding: 2rem;
}
.card-sidebar__heading, .card-sidebar__text {
  color: #fff;
  margin: 1rem 0;
}
.card-sidebar__label {
  font-weight: 700;
}

.event {
  padding: 5rem 0;
}
.event__heading {
  text-align: center;
  margin: 0;
  color: #484349;
  margin-bottom: 5rem;
}
.event__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .event__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.event__info {
  grid-column: 1/3;
}